import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';

@Component({
  selector: 'app-mod-pdv',
  templateUrl: './mod-pdv.page.html',
  styleUrls: ['./mod-pdv.page.scss'],
})
export class ModPDVPage implements OnInit {

  id: number;
  Des_de_alimnentos: string="";
  Unidad_de_Media: string="";
  Gr_ML_Desayuno: string="";
  Gr_ML_Sopa: string="";
  Gr_ML_Segundo: string="";
  Gr_ML_Cena: string="";


  disabledbutton;

  constructor(
    private router: Router,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private accsPrvds: AccessProviders,
    private actRoute: ActivatedRoute
  ) { }

  ngOnInit() {
    this.actRoute.params.subscribe((data: any)=>{
      console.log(data);
      this.id = data.id;

      if(this.id != 0){
        this.cargarUser()
      }
    });
  }

  cargarUser(){
    return new Promise(resolve =>{
      let body = {
        aksi: 'load_single_dataPDV', 
        id: this.id
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res: any) => {
        this.Des_de_alimnentos = res.result.Des_de_alimnentos;
        this.Unidad_de_Media = res.result.Unidad_de_Media;
        this.Gr_ML_Desayuno = res.result.Gr_ML_Desayuno;
        this.Gr_ML_Sopa = res.result.Gr_ML_Sopa;
        this.Gr_ML_Segundo = res.result.Gr_ML_Segundo;
        this.Gr_ML_Cena = res.result.Gr_ML_Cena;
      });
    });
  }


  async crudActionguardar(a){
    if(this.Des_de_alimnentos=="",
       this.Unidad_de_Media=="",
       this.Gr_ML_Desayuno=="",
       this.Gr_ML_Sopa=="",
       this.Gr_ML_Segundo=="",
       this.Gr_ML_Cena==""
    ){
 
    }else{
      this.disabledbutton = true;
      const loader = await this.loadingCtrl.create({
        message: 'Espere por favor....',
      });
      await loader.present();

      return new Promise(resolve =>{
        let body = {
          aksi: 'mod_crudPDV',
          id: this.id,
          Des_de_alimnentos: this.Des_de_alimnentos,
          Unidad_de_Media: this.Unidad_de_Media,
          Gr_ML_Desayuno: this.Gr_ML_Desayuno,
          Gr_ML_Sopa: this.Gr_ML_Sopa,
          Gr_ML_Segundo: this.Gr_ML_Segundo,
          Gr_ML_Cena: this.Gr_ML_Cena,
          action: a
        }

        this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
          if(res.success==true){
            loader.dismiss();
            this.disabledbutton = false;
            this.presentToast(res.msg);
            this.router.navigate(['/menu-admin']);
          }else{
            loader.dismiss();
            this.disabledbutton = false;
            this.presentAlert(res.msg, a);
          }
        },(err)=>{
          loader.dismiss();
          this.disabledbutton = false;
          this.presentAlert('Timeout',a);
        });
      });
    }
  }

  
  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500,
    });
    toast.present();
  }

  async presentAlert(a,b) {
    const alert = await this.alertCtrl.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Close',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Try Again',
          handler: () => {
            this.crudActionguardar(b);
          }
        }
      ]
    });

    await alert.present();
  }
  

}
